import './app.scss'
import { useEffect } from 'react';
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux'
import { currentUser } from './actions/userActions'
import { currentCart } from './actions/cartActions'
import MaintenancePage from './components/MaintenancePage'
import Navbar from './components/Navbar';
import Footer from './components/Footer';
import Home from './components/Home';
import Products from './components/Products';
import AuthRoute from './components/AuthRoute';
import SignInPage from './components/SignInPage';
import SignUpPage from './components/SignUpPage';
import Cart from './components/Cart'
import InfoForm from './components/InfoForm'
import ConfirmForm from './components/ConfirmForm'
import CheckoutForm from './components/CheckoutForm'
import Success from './components/Success'
import Newsletter from './components/Newsletter'
import AdminAuth from './components/AdminAuth'
import Admin from './components/Admin'
import About from './components/About'
import Faq from './components/Faq'
import Reviews from './components/Reviews'

export default function App() {
	const dispatch = useDispatch()
	const user = useSelector(state=> state.currentUser)
  
	useEffect(()=>{
		dispatch(currentUser())
		dispatch(currentCart())
	}, [dispatch])

  	return (
		<BrowserRouter>
			{process.env.REACT_APP_MAINTENANCE ? (
				<Switch>
					<Route component={MaintenancePage} />
		  		</Switch>
			) : (
				<>
				<Navbar />
				<Switch>
					<Route path='/about' render={(routeProps)=><About {...routeProps}/>} />
					<Route path='/faq' render={(routeProps)=><Faq {...routeProps}/>} />
					<Route path='/reviews' render={(routeProps)=><Reviews {...routeProps}/>} />
					<AdminAuth path='/admin' isAuth={user} component={Admin} />
					<AuthRoute path='/success' isAuth={user} component={Success} />
					<Route path='/products' render={(routeProps)=><Products {...routeProps}/>} />
					<Route path='/cart' render={(routeProps)=><Cart {...routeProps}/>} />
					<Route path='/info' render={(routeProps)=><InfoForm {...routeProps}/>} />
					<Route path='/checkout' render={(routeProps)=><CheckoutForm {...routeProps}/>} />
					<Route path='/confirm' render={(routeProps)=><ConfirmForm {...routeProps}/>} />
					<Route path='/newsletter' render={(routeProps)=><Newsletter {...routeProps}/>} />
					<Route path='/sign_in' render={(routeProps)=><SignInPage {...routeProps}/>} />
					<Route path='/sign_up' render={(routeProps)=><SignUpPage {...routeProps}/>} />
					<Route path='/' component={Home} />
				</Switch>
				<Footer />
				</>
			)}
		</BrowserRouter>
	)
}